import { notification } from 'antd';
import { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

export default function usePosPin() {
	const dispatch = useDispatch();
	const history = useHistory();

	const { loading, isLog: isAuthLog, authorized: isAuthorized } = useSelector((state) => state.auth);

	const [code, setCode] = useState('');
	const ref = useRef([]);

	const handleSetCode = (key) => {
		if (code.length < ref.current.inputs.length) {
			setCode((code) => `${code}${key}`);
			return;
		}

		setCode((code) => `${code.slice(0, -1)}${key}`);
		return;
	};

	const handleSubmit = (pin) => {
		try {
			if (pin !== '') {
				dispatch({
					type: 'auth/LOGIN_PIN',
					payload: {
						pinCode: pin
					},
					history
				});

				return;
			}

			notification.error({
				message: 'Perhatian!',
				description: 'Silahkan masukkan PIN terlebih dahulu'
			});
		} catch (error) {
			console.log(error);
		} finally {
			setCode('');
			ref.current.inputs[0].focus();
			ref.current.inputs.forEach((input) => {
				input.value = '';
			});
		}
	};

	const keys = [
		{
			key: '1',
			type: 'number'
		},
		{
			key: '2',
			type: 'number'
		},
		{
			key: '3',
			type: 'number'
		},
		{
			key: '4',
			type: 'number'
		},
		{
			key: '5',
			type: 'number'
		},
		{
			key: '6',
			type: 'number'
		},
		{
			key: '7',
			type: 'number'
		},
		{
			key: '8',
			type: 'number'
		},
		{
			key: '9',
			type: 'number'
		},
		{
			key: 'Delete',
			type: 'function'
		},
		{
			key: '0',
			type: 'number'
		},
		{
			key: 'Enter',
			type: 'function'
		}
	];

	const handleKeyboard = (e) => {
		const key = (e?.key || '').toLowerCase();

		if (+key >= 0 || +key <= 9) {
			handleSetCode(key);
		}

		if (key === 'backspace' || key === 'delete') {
			ref.current.inputs.forEach((input) => {
				input.value = '';
			});

			setCode('');
			ref.current.inputs[0].focus();
		}

		if (key === 'enter') {
			handleSubmit(code);
		}
	};

	useEffect(() => {
		if (ref && ref.current.inputs.length !== 0) {
			ref.current.inputs[0].focus();
		}

		const app = localStorage.getItem('app');
		if (app && app !== null) {
			const json = JSON.parse(app);
			const { isLog = false, authorized = false } = json;

			if ((isLog && authorized) || (isAuthLog && isAuthorized)) {
				history.push('/pos/kasir');
				return;
			} else if ((!isLog && authorized) || (!isAuthLog && isAuthorized)) {
				history.push('/pos/auth');
				return;
			}

			history.push('/auth/login');
		}
	}, []);

	useEffect(() => {
		document.addEventListener('keydown', handleKeyboard);

		return () => {
			document.removeEventListener('keydown', handleKeyboard);
		};
	}, [code]);

	useEffect(() => {
		if (ref.current.inputs) {
			const input = ref.current.inputs[0];

			if (input) {
				input.focus();
			}
		}
	}, [ref]);

	useEffect(() => {
		if (code !== '') {
			ref.current.inputs.forEach((input, index) => {
				if (code.length <= ref.current.inputs.length) {
					input.value = code[index] || '';
					return;
				}

				// assign input value with last index of code
				ref.current.inputs[ref.current.length - 1].value = code[code.length - 1];
			});
		}
	}, [ref, code]);

	return { ref, code, setCode, handleSubmit, handleSetCode, handleKeyboard, keys, loading };
}
