/* eslint-disable no-unused-vars */
import { Button } from 'antd';
import React from 'react';
import ReactPinField from 'react-pin-field';
import usePosPin from './hooks/usePosPin';

const PinPosPage = () => {
	const { ref, code, setCode, keys, handleSubmit, handleSetCode, loading } = usePosPin();

	return (
		<div className="w-full h-screen bg-white flex items-center justify-center">
			<div className="w-full md:w-max h-full md:h-max bg-white md:p-5 py-12 md:rounded-xl flex flex-col items-center justify-center gap-5">
				<div className="flex">
					<div className="m-auto text-center">
						<h1 className="text-xl font-light my-5 text-gray-600">Enter the PIN code</h1>
						<ReactPinField
							ref={ref}
							data-cy="pin-field"
							className="pin-field"
							// type="password"
							validate="0123456789"
							autoFocus
							autoCapitalize="off"
							autoCorrect="on"
							autoComplete="on"
							inputMode="numeric"
							length="6"
							onChange={setCode}
						/>
					</div>
				</div>
				<div className="mt-5">
					<div className="w-full text-center">
						<div className="w-full grid grid-cols-3 gap-3">
							{keys?.map(({ key, type }) => {
								return (
									<Button
										key={key}
										loading={key === 'Enter' ? loading : false}
										className={` ${key === 'Enter' ? 'bg-blue-600' : 'bg-gray-100'} ${
											key === 'Enter' && 'text-white'
										} rounded-lg hover:bg-blue-500 hover:text-white transition-all ease-in-out`}
										style={{ width: '75px', height: '75px' }}
										onClick={() => {
											if (type === 'number') {
												handleSetCode(key);
											}

											if (type === 'function') {
												if (key === 'Delete') {
													setCode('');
													ref.current.forEach((input) => (input.value = ''));
													ref.current[0].focus();
												} else if (key === 'Enter') {
													handleSubmit(code);
												}
											}

											return;
										}}>
										{key}
									</Button>
								);
							})}
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default PinPosPage;
